import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Link } from "react-router-dom";
import Footer from './Footer'
import apiurl from "./config";

function Register() {
  const [output, setOutput] = useState("");
  const [username, setusername] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [confirm_password, setconfirm_password] = useState("");
  const [gender, setGender] = useState("");

  const navigate = useNavigate();
  const handleSubmit = async (evt) => {
    evt.preventDefault();

    if (!username) {
      setOutput("Please enter your name.");
      return;
    }

    if (!email) {
      setOutput("Please enter your email address.");
      return;
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setOutput("Please enter a valid email address.");
      return;
    }

    if (!password) {
      setOutput("Please enter a password.");
      return;
    }

    if (!confirm_password) {
      setOutput("Please enter a confirm password.");
      return;
    }

    if (password !== confirm_password) {
      setOutput("Password & Confirm  should be same.");
      return;
    }

    if (!gender) {
      setOutput("Please select gender.");
      return;
    }
    let userDetails = { username, email, password, confirm_password, gender };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/signup`,
        userDetails
      );

      if (response.data.token !== "error") {
        setOutput("User registered successfully.");
        toast.success(response.data.msg);

        setTimeout(() => {
          navigate("/login");
        }, 3000);
        // navigate("/login")
        setusername("");
        setEmail("");
        setPassword("");
        setconfirm_password("");
        setGender("");
      } else {
        setOutput(response.data.message);
      }
    } catch (error) {
      if (
        error.response &&
        error.response.data &&
        error.response.data.result === "false" &&
        error.response.data.message ===
          "user allready registered, please go to login page.."
      ) {
        setOutput(
          "The email is already registered. Please go to the login page."
        );
      } else {
        //console.log(error);
      }
    }
  };

  return (
   <>
      <ToastContainer />
    <section className="login-main-wrapper">
    <div className="container-fluid pl-0 pr-0">
      <div className="row no-gutters">
   
        <div className="col-md-5 bg-white full-height p-5  "  style={{textAlign:'center'}}  >
         
                
                  <img style={{height:'450px'}} src="img/login.png" className="img-fluid" alt="LOGO" />
                  <h2 className="mt-5 mb-3">Welcome to Yuonair</h2>
                  <p className="mb-4">when an unknown printer took a galley of type and scrambled<br /> it to make a type specimen book. It has survived not <br />only five centuries</p>
               
              
        </div>
        <div className="col-md-7 p-5 bg-white full-height">
          <div className="login-main-left">
            <div className="text-center mb-5 login-main-left-header pt-1">
              <img src="img/favicon.png" className="img-fluid" alt="LOGO" />
              <h2 className="mt-1 mb-0">Welcome to Yuonair</h2>
              {/* <p>It is a long established fact that a reader <br /> will be distracted by the readable.</p> */}
            </div>
            {output && <font style={{ color: "red" }}>{output}</font>}
              <form onSubmit={handleSubmit}>
                    <div className="form-group">
                      <label>UserName</label>
                      <input type="text" className="form-control"   value={username}
                    onChange={(e) => setusername(e.target.value)} placeholder="Enter Username" />
                      <span className="form-validation" style={{ color: "red" }} ></span>
                      <span className="form-validation" style={{ color: "red" }} ></span>
                    </div>
                   
                    <div className="form-group">
                      <label>Email</label>
                      <input type="email" className="form-control" name="email" value={email}
                    onChange={(e) => setEmail(e.target.value)}  placeholder="Enter email" />
                      <br /><span className="form-validation" style={{ color: "red" }}></span>
                      <span className="form-validation" style={{ color: "red" }}></span>
                    </div>
                    <div className="form-group">
                      <label>Password</label>
                      <input type="password" className="form-control" maxLength={8} name="password" value={password}
                    onChange={(e) => setPassword(e.target.value)} placeholder="Password" />
                      <br /><span className="form-validation" id='unicpass' style={{ color: "red" }}></span>

                    </div>
                    <div className="form-group">
                      <label>Confirm Password</label>
                      <input type="password" className="form-control" maxLength={'8'} name="password2" value={confirm_password}
                    onChange={(e) => setconfirm_password(e.target.value)} placeholder="confirm Password" />
                      <br /><span className="form-validation" style={{ color: "red" }}></span>
                    </div>
                    <div className="form-group">
  <label>Gender</label>
  <select className="form-control" name="gender" value={gender} onChange={(e) => setGender(e.target.value)}>
    <option value="">Select gender</option>
    <option value="male">Male</option>
    <option value="female">Female</option>
    <option value="other">Other</option>
  </select>
  <br /><span className="form-validation" style={{ color: "red" }}></span>
</div>


                    <div className="mt-4">
                      <button type="submit" className="btn btn-outline btn-block btn-lg" style={{ backgroundColor: '#000080', color: 'white', borderRadius: '20px' }}>Sign Up</button>
                    </div>
                  </form>
              <div className="text-center mt-5">
                <p className="light-gray">
                  Already have an Account? <Link to="/login"><strong>Sign In</strong></Link>
                </p>
              </div>
          </div>
        </div>


      </div>
    </div>
  </section>
    <br/>
    <br/><br/>
    <br/>
    <br/>
    <br/><br/>
    
   <Footer/>
   </>
  );
}

export default Register;
