import React from 'react'
import Navbar from './Navbar'
import Sidebar from './Sidebar'
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import apiurl from "./config";
import secureLocalStorage from "react-secure-storage";

const History = () => {
  const [getcommt, setgetcomment] = useState([]);

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();

    const timeDiff = Math.abs(now - date);

    const years = Math.floor(timeDiff / (1000 * 60 * 60 * 24 * 365));
    if (years > 0) {
      return years === 1 ? "1 year ago" : `${years} years ago`;
    }

    const months = Math.floor(timeDiff / (1000 * 60 * 60 * 24 * 30));
    if (months > 0) {
      return months === 1 ? "1 month ago" : `${months} months ago`;
    }

    const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
    if (days > 0) {
      return days === 1 ? "1 day ago" : `${days} days ago`;
    }

    const hours = Math.floor(timeDiff / (1000 * 60 * 60));
    if (hours > 0) {
      return hours === 1 ? "1 hour ago" : `${hours} hours ago`;
    }

    const minutes = Math.floor(timeDiff / (1000 * 60));
    if (minutes > 0) {
      return minutes === 1 ? "1 minute ago" : `${minutes} minutes ago`;
    }

    return "Just now";
  };

  useEffect(() => {
    gethistory();
  }, []);

  const gethistory = async () => {
    const idddd = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = JSON.stringify({
      user_id: idddd,
    });

    try {
      const res = await axios.post(
        `${apiurl[0].apiUrl}api/get_history`,
        data,
        options
      );
      setgetcomment(res.data.data);
    } catch (err) {
      //console.error("API Error:", err);
    }
  };

  const removeCartitem = (item) => {
    const idddd = secureLocalStorage.getItem("_id");
    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = JSON.stringify({
      user_id: "651a9440cdafe45529517210",
      video_id: item,
    });

    axios
      .post(`${apiurl[0].apiUrl}api/delete_history`, data, options)
      .then((res) => {
        gethistory();
      })
      .catch((err) => {
        //console.error(err);
      });
  };

  let [status, setstatus] = useState(true);

  setTimeout(() => {
    setstatus(false);
  }, 2000);





  return (
    <div>
   <Navbar/>
    <div id="wrapper">
      {/* Sidebar */}
    <Sidebar/>
      <div id="content-wrapper">
        <div className="container-fluid">
          <div className="video-block section-padding">
            <div className="row">
              {/* <div className="col-md-12">
                <div className="main-title">
                  
                  <h6>Watch History</h6>
                </div>
              </div> */}
              {getcommt && getcommt.length > 0 ? (
                  getcommt.map((list) => (

              <div className="col-xl-3 col-sm-6 mb-3" key={list._id}>
                <div className="video-card history-video">
                  <div className="video-card-image">
                    <a onClick={() => removeCartitem(list.video_id)} className="video-close" href="#"><i className="fas fa-times-circle" /></a>
                    <Link to='/videopage'     onClick={() => {
                              secureLocalStorage.setItem("videoiid", list.video_id);
                              secureLocalStorage.setItem("useridd", list.user_id);
                              secureLocalStorage.setItem(
                                "channelid",
                                list.channel_id
                              );
                              secureLocalStorage.setItem(
                                "categorytpee",
                                list.data[0].category_type
                              );
                            }} className="play-icon" href="#"><i className="fas fa-play-circle" /></Link>

                    <Link
                            to="/videopage"
                        
                          >
                            {list?.data &&
                              list?.data[0]?.video &&
                              list.data[0]?.video[1]?.filename && (
                                <img
                                  
                                  className="img-fluid"
                                  src={`${apiurl[0].apiUrl}uploads/${list?.data[0]?.video[1]?.filename}`}
                                  alt=""
                                />
                              )}
                          </Link>



                    
                    <div className="time">3:50</div>
                  </div>
              
                  <div className="video-card-body">
                    <div className="video-title">
                      <a href="#">

                        {list.data[0]?.description.charAt(0).toUpperCase() + list.data[0]?.description.slice(1)}
                        </a>
                    </div>
                    <div className="video-page text-success">
                    {list.data[0]?.channel_name.charAt(0).toUpperCase() + list.data[0]?.channel_name.slice(1)}  &nbsp;
                        <a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></a>
                    </div>
                    <div className="video-view">
                    {list.data && list.data[0]?.video_views} views &nbsp;<i className="fas fa-calendar-alt" /> {formatDate(list?.data[0]?.current_date)}
                    </div>
                  </div>
                </div>
              </div>
             
             ))
                ) : (
                  <div className="col-md-12">
                    {status == true ? (
                      <div className="row justify-content-center">
                        <div class="spinner-border text-primary"></div>
                      </div>
                    ) : (
                      <div className="row justify-content-center">
                        <div></div>
                      </div>
                    )}
                  </div>
                )}
            </div>
            
          </div>
        </div>
        
     
      </div>

    </div>
    
    

  </div>
  )
}

export default History;
