import secureLocalStorage from "react-secure-storage";

import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import apiurl from "./config";

const Login = () => {
  const navigate = useNavigate();
  const [output, setOutput] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [currentToken, setCurrentToken] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (evt) => {
    evt.preventDefault();

    if (!email) {
      setOutput("*Email is required");
      return;
    } else if (!password) {
      setOutput("*Password is required");
      return;
    }
    let fcm_id = localStorage.getItem("yuonair_fcm");
    const userDetails = {
      email: email,
      password: password,
      fcm: "",
      //fcm: fcm_id,
    };

    axios
      .post(`${apiurl[0].apiUrl}api/login`, userDetails)
      .then((response) => {
        if (response.data.result) {
          const responseData = response.data.userDetails;
          secureLocalStorage.setItem("_id", response.data.data._id);
          secureLocalStorage.setItem("username", response.data.data.username);
          secureLocalStorage.setItem("email", response.data.data.email);

          secureLocalStorage.setItem("gender", response.data.data.gender);
          secureLocalStorage.setItem("role", response.data.data.role);
          // localStorage.setItem("fcmm", response.data.data.fcm);

          toast.success(response.data.msg);

          setTimeout(() => {
            navigate("/");
          }, 3000);
        } else {
          if (response.data.message === "User not found") {
            setOutput("Invalid user. Please register first.");
          } else {
            setOutput("Invalid user or verify your account.");
          }
          setEmail("");
          setPassword("");
        }
      })
      .catch((err) => {
        //console.log(err);
        setOutput("Incorrect email & password");
      });
  };

  return (
    


    <section className="login-main-wrapper">
         <ToastContainer />
    <div className="container-fluid pl-0 pr-0">
      <div className="row no-gutters">
   
        <div className="col-md-5 bg-white full-height p-5  "  style={{textAlign:'center'}}  >
         
                
                  <img style={{height:'450px'}} src="img/login.png" className="img-fluid" alt="LOGO" />
                  <h2 className="mt-5 mb-3">Welcome to Yuonair</h2>
                  <p className="mb-4">when an unknown printer took a galley of type and scrambled<br /> it to make a type specimen book. It has survived not <br />only five centuries</p>
               
              
        </div>
        <div className="col-md-7 p-5 bg-white full-height">
          <div className="login-main-left">
            <div className="text-center mb-5 login-main-left-header pt-4">
              <img src="img/favicon.png" className="img-fluid" alt="LOGO" />
              <h2 className="mt-3 mb-3">Welcome to Yuonair</h2>
              {/* <p>It is a long established fact that a reader <br /> will be distracted by the readable.</p> */}
            </div>
            <font style={{ color: "red" }}>{output}</font>
                  <form onSubmit={handleSubmit} text="center">
                    <div className="form-group">
                      <label>Email</label>
                      <input
                        type="email"
                        name="email"
                        placeholder="Email Addres"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        className="form-control"
                      />
                      <br />
                      <span
                        className="form-validation"
                        style={{ color: "red" }}
                      ></span>
                    </div>
                    <div className="form-group">
                      <label>Password</label>
                      <input
                        type="password"
                        id="form-login-password-2"
                        name="password"
                        placeholder="Password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        className="form-control"
                      />
                      <br />
                      <span
                        className="form-validation"
                        id="unicpass"
                        style={{ color: "red" }}
                      ></span>
                    </div>
                    <div className="mt-4">
                      <div className="row">
                        <div className="col-12">
                        <p className="light-gray">Change your password ? <Link to="/forgotpassword"><strong>Reset-Password</strong></Link></p>
                          <button
                            type="submit"
                            className="btn btn-outline btn-block btn-lg"
                            style={{
                              backgroundColor: "#000080",
                              color: "white",
                              borderRadius: 20,
                            }}
                          >
                            Login
                          </button>
                        </div>
                      </div>
                    </div>
                  </form>
            <div className="text-center mt-5">

              <p className="light-gray">Dont have an account? <Link to="/register"><strong>Sign Up</strong></Link></p>
            </div>
          </div>
        </div>


      </div>
    </div>
  </section>
  );
}

export default Login;