import React, { useEffect } from "react";
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import { useState } from "react";

import axios from "axios";
import { useNavigate } from "react-router-dom";
import apiurl from "./config";
import secureLocalStorage from "react-secure-storage";
const Setting = () => {
  const [selectedProfileImage, setSelectedProfileImage] = useState(null);
  const [selectedCoverImage, setSelectedCoverImage] = useState(null);
  const [description, setDescription] = useState("");
  const [channelname, setChannelname] = useState("");
  const [error, setError] = useState("");
  const [successMessage, setSuccessMessage] = useState("");
  const [listsa, setListsa] = useState([]);
  const Navigate = useNavigate();
  const chanelid = secureLocalStorage.getItem("channel_id");
  console.log("selectedProfileImagesadsadasd", selectedProfileImage);
  console.log("selectedCoverImage", selectedCoverImage);

  const handleProfileImageSelect = (event) => {
    setSelectedProfileImage(event.target.files[0]);
  };

  const handleCoverImageSelect = (event) => {
    setSelectedCoverImage(event.target.files[0]);
  };

  const handleFormSubmit = async (event) => {
    event.preventDefault();

    if (!selectedProfileImage) {
      setError("Please select a Profile image.");
      return;
    }

    if (!selectedCoverImage) {
      setError("Please select a Cover image.");
      return;
    }
    const formData = new FormData();

    formData.append("profile_image", selectedProfileImage);
    formData.append("cover_image", selectedCoverImage);
    formData.append("channel_name", channelname);
    formData.append("handle", description);
    formData.append("_id", chanelid);

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/update_channel`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            "X-Api-Key": "your-api-key",
          },
        }
      );

      setSelectedProfileImage(null);
      setSelectedCoverImage(null);
      setChannelname("");
      setDescription("");
      setError("");
      setSuccessMessage("Channel updated successfully!");
      Navigate("/myaccount");
    } catch (error) {}
  };

  useEffect(() => {
    getHomeData22();
  }, []);

  const getHomeData22 = async () => {
    const userid = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: userid,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/get_my_channel`,
        data,
        options
      );

      if (response.data.data && response.data.data.length > 0) {
        const channelName = response.data.data[0].channel_name;
        const channelid = response.data.data[0]._id;

        secureLocalStorage.setItem("channel_name", channelName);
        secureLocalStorage.setItem("channel_id", channelid);

        setListsa(response.data.data);
      }
    } catch (err) {
      //console.error(err);
    }
  };
  return (
    <div>
      <Navbar />
      <div id="wrapper">
        {/* Sidebar */}
        <Sidebar />
        <div id="content-wrapper">
          <div className="container-fluid upload-details card-footer">
            <div className="row">
              <div className="col-lg-12">
                <div className="main-title">
                  <h6>Update Channel</h6>
                </div>
              </div>
            </div>
            <font style={{ color: "blue" }}>
              <p>{successMessage}</p>
            </font>
            <font style={{ color: "red" }}>{error && <p>{error}</p>}</font>
            <form onSubmit={handleFormSubmit}>
              {listsa?.map((images) => {
                return (
                  <div className="modal-body">
                    <div className="single-channel-image">
                      <div style={{ height: "205px", width: "100%" }}>
                        <img
                          style={{ height: "200px" }}
                          width={"100%"}
                          src={
                            `${apiurl[0].apiUrl}uploads/` +
                            images?.image[0]?.filename
                          }
                          alt=""
                          className="img-fluid"
                        />
                      </div>

                      <div
                        className="channel-profile mainprofileimage"
                        style={{ position: "absolute", top: "70px" }}
                      >
                        <img
                          className="channel-profile-img profileimg mainprofile"
                          width={"100%"}
                          src={
                            `${apiurl[0].apiUrl}uploads/` +
                            images?.image[1]?.filename
                          }
                        />
                      </div>
                    </div>
                  </div>
                );
              })}

              <div>
                <label htmlFor="profile-picture-input">
                  <span className="btn btn-info">Profile picture</span>
                  <input
                    id="profile-picture-input"
                    type="file"
                    name="profile-picture"
                    onChange={handleProfileImageSelect}
                    style={{ display: "none" }}
                  />
                </label>
                <br />
                <br />
                <label htmlFor="cover-image-input">
                  <span className="btn btn-info">Cover Photo</span>
                  <input
                    id="cover-image-input"
                    type="file"
                    onChange={handleCoverImageSelect}
                    style={{ display: "none" }}
                  />
                </label>
              </div>
              <br />
              <div className="form-group">
                <label htmlFor="channelname">Channel Name:</label>
                <input
                  required
                  type="text"
                  placeholder="Enter Channel Name"
                  value={channelname}
                  onChange={(e) => setChannelname(e.target.value)}
                  className="form-control profile1"
                  id="channelname"
                />
              </div>

              <div className="form-group">
                <label htmlFor="description">Description:</label>
                <input
                  required
                  type="text"
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                  className="form-control profile2"
                  id="description"
                />
              </div>
              <br />
              <button className="btn btn-info" type="submit">
                Submit
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Setting;
