import React from "react";
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import apiurl from "./config";
import secureLocalStorage from "react-secure-storage";

const Watchlater = () => {
  const [getdownloadd, setGetdownloadd] = useState([]);
  console.log("getdownloadd", getdownloadd);
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();

    const timeDiff = Math.abs(now - date);

    const years = Math.floor(timeDiff / (1000 * 60 * 60 * 24 * 365));
    if (years > 0) {
      return years === 1 ? "1 year ago" : `${years} years ago`;
    }

    const months = Math.floor(timeDiff / (1000 * 60 * 60 * 24 * 30));
    if (months > 0) {
      return months === 1 ? "1 month ago" : `${months} months ago`;
    }

    const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
    if (days > 0) {
      return days === 1 ? "1 day ago" : `${days} days ago`;
    }

    const hours = Math.floor(timeDiff / (1000 * 60 * 60));
    if (hours > 0) {
      return hours === 1 ? "1 hour ago" : `${hours} hours ago`;
    }

    const minutes = Math.floor(timeDiff / (1000 * 60));
    if (minutes > 0) {
      return minutes === 1 ? "1 minute ago" : `${minutes} minutes ago`;
    }

    return "Just now";
  };

  useEffect(() => {
    getdownload();
  }, []);

  const getdownload = async () => {
    const userid = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: userid,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/get_save_watch_later`,
        data,
        options
      );
      setGetdownloadd(response.data.data);
      //console.log("sdasdas",response.data.data)
    } catch (err) {
      //console.error(err);
    }
  };

  const removeCartitem = (item) => {
    const idddd = secureLocalStorage.getItem("_id");
    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = JSON.stringify({
      user_id: idddd,
      video_id: item,
    });

    axios
      .post(`${apiurl[0].apiUrl}api/delete_save_watch_later`, data, options)
      .then((res) => {
        getdownload();
      })
      .catch((err) => {});
  };

  let [status, setstatus] = useState(true);

  setTimeout(() => {
    setstatus(false);
  }, 1000);

  return (
    <div>
   <Navbar/>
    <div id="wrapper">
      {/* Sidebar */}
     <Sidebar/>
      <div id="content-wrapper">
        <div className="container-fluid pb-0">
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-5">
                <div className="single-video-left">
                {getdownloadd.slice(0, 1)?.map((list) => {
                      return (
                        <div
                          className="col-xl-12 col-sm-12 mb-3"
                          key={list._id}
                        >
                          <div
                            style={{
                              height: "680px",
                              background: "rgb(191 191 191)",
                              padding: "30px",
                              borderRadius: "10px",
                            }}
                            className="video-card history-video "
                          >
                            <div className="video-card-image">
                              <a
                                onClick={() => removeCartitem(list.video_id)}
                                className="video-close"
                                href="#"
                              >
                                <i className="fas fa-times-circle" />
                              </a>
                              <Link
                                to="/videopage"
                                onClick={() => {
                                  secureLocalStorage.setItem(
                                    "videoiid",
                                    list.video_id
                                  );
                                  secureLocalStorage.setItem(
                                    "useridd",
                                    list.user_id
                                  );
                                  secureLocalStorage.setItem(
                                    "channelid",
                                    list.channel_id
                                  );
                                  secureLocalStorage.setItem(
                                    "categorytpee",
                                    list.video_data[0].category_type
                                  );
                                }}
                                className="play-icon"
                                href="#"
                              >
                                <i className="fas fa-play-circle" />
                              </Link>

                              <Link to="/videopage">
                                <img
                                  className="img-fluid"
                                  src={
                                    `${apiurl[0].apiUrl}uploads/` +
                                    list?.channel_data[0]?.image[1]?.filename
                                  }
                                  alt=""
                                />
                              </Link>

                              <div className="time">3:50</div>
                            </div>

                            <div className="video-card-body">
                              <div className="video-title">
                                <h3 style={{color:'white' , fontWeight:'700px'}}>Watch later</h3>
                                <a  href="#">
                                  {list?.video_data[0]?.video_name
                                    .charAt(0)
                                    .toUpperCase() +
                                    list?.video_data[0]?.video_name.slice(1)}
                                </a>
                              </div>
                              <div className="video-page text-success">
                                {list?.channel_data[0]?.channel_name
                                  .charAt(0)
                                  .toUpperCase() +
                                  list?.channel_data[0]?.channel_name.slice(
                                    1
                                  )}{" "}
                                &nbsp;
                                <a
                                  title
                                  data-placement="top"
                                  data-toggle="tooltip"
                                  href="#"
                                  data-original-title="Verified"
                                >
                                  <i className="fas fa-check-circle text-success" />
                                </a>
                              </div>
                              <div className="video-view pt-2">
                                {list?.video_data[0]?.video_views} views &nbsp;
                                <i className="fas fa-calendar-alt" />{" "}
                                {formatDate(list?.video_data[0]?.current_date)}
                              </div>

                              <div className="pt-3">
                                <button
                                  style={{ borderRadius: "10px" }}
                                  className="btn bttn"
                                >
                                  <i class="fas fa-play-circle"></i> Play Now
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      );
                    })}
                  
                </div>
              </div>










              <div className="col-md-7 pl-4">
                <div className="single-video-right">
                  <div className="row">
                    <div className="col-md-12">
                      
                      <div className="main-title">
                       
                        <h6>Videos</h6>
                      </div>
                    </div>
                    <div className="col-md-12">
                    {getdownloadd?.map((list) => {
                      return (
                        <div className="video-card video-card-list">
                          <div className="video-card-image">
                          <a
                                onClick={() => removeCartitem(list.video_id)}
                                className="video-close"
                                href="#"
                              >
                                <i className="fas fa-times-circle" />
                              </a>
                            <a className="play-icon" href="#">
                              <i className="fas fa-play-circle" />
                            </a>
                            <a href="#">
                              <img 
                                className="img-fluid"
                                src={
                                  `${apiurl[0].apiUrl}uploads/` +
                                  list?.channel_data[0]?.image[1]?.filename
                                }
                                alt=""
                              />
                            </a>
                            <div className="time">3:50</div>
                          </div>
                          <div className="video-card-body">
                            <div className="btn-group float-right right-action">
                              <a
                                href="#"
                                className="right-action-link text-gray"
                                data-toggle="dropdown"
                                aria-haspopup="true"
                                aria-expanded="false"
                              >
                                <i
                                  className="fa fa-ellipsis-v"
                                  aria-hidden="true"
                                />
                              </a>
                              <div className="dropdown-menu dropdown-menu-right">
                                <a className="dropdown-item" href="#">
                                  <i className="fas fa-fw fa-star" /> &nbsp;
                                  Top Rated
                                </a>
                                <a className="dropdown-item" href="#">
                                  <i className="fas fa-fw fa-signal" />{" "}
                                  &nbsp; Viewed
                                </a>
                                <a className="dropdown-item" href="#">
                                  <i className="fas fa-fw fa-times-circle" />{" "}
                                  &nbsp; Close
                                </a>
                              </div>
                            </div>
                            <div className="video-title">
                              <a href="#">
                                {list?.video_data[0]?.video_name
                                  .charAt(0)
                                  .toUpperCase() +
                                  list?.video_data[0]?.video_name.slice(1)}
                              </a>
                            </div>
                            <div className="video-page text-success">
                              {list?.channel_data[0]?.channel_name
                                .charAt(0)
                                .toUpperCase() +
                                list?.channel_data[0]?.channel_name.slice(
                                  1
                                )}{" "}
                              <a
                                title
                                data-placement="top"
                                data-toggle="tooltip"
                                href="#"
                                data-original-title="Verified"
                              >
                                <i className="fas fa-check-circle text-success" />
                              </a>
                            </div>
                            <div className="video-view">
                              {list?.video_data[0]?.video_views} views
                              &nbsp;
                              <i className="fas fa-calendar-alt" />{" "}
                              {formatDate(
                                list?.video_data[0]?.current_date
                              )}
                            </div>
                          </div>
                        </div>
                      );
                    })}
                     
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        
      </div>
     
    </div>
  
  </div>
  );
};

export default Watchlater;
