import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import React from "react";
import { Link, useNavigate, useSearchParams } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import axios from "axios";

import { BiSearchAlt } from "react-icons/bi";
import { BsSearch } from "react-icons/bs";
import apiurl from "./config";
import secureLocalStorage from "react-secure-storage";

function Navbar() {
  let Navigate = useNavigate();

  const [count, setCount] = useState();
  const [searchTerm, setSearchTerm] = useState([]);
  const [searchResults, setSearchResults] = useState([]);
  const [listsa, setListsa] = useState([]);
  const [lists, setLists] = useState([]);
  let [showsearch, setshowsearch] = useState([]);

  const [selectedProfileImage, setSelectedProfileImage] = useState(null);
  const [selectedCoverImage, setSelectedCoverImage] = useState(null);
  const [description, setDescription] = useState("");
  const [channelname, setChannelname] = useState("");

  const [error, setError] = useState("");
  const [successMessage, setSuccessMessage] = useState("");

  const usered = secureLocalStorage.getItem("_id");
  let [showp_image, setpimage] = useState();
  let [cover_iamge, setcimage] = useState();
  const [selectedImage, setSelectedImage] = useState(null);

  const handleImageSelect = (event) => {
    setSelectedImage(event.target.files[0]);
  };

  const handleFormSubmitt = async (event) => {
    event.preventDefault();
    const formData = new FormData();
    formData.append("profile_image", selectedImage);
    formData.append("_id", usered);

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/upload_user_image`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );
    } catch (error) {
      //console.error("Error updating profile picture:", error.response.data);
    }
  };

  useEffect(() => {
    getHomeData2();
  }, []);

  const getHomeData2 = async () => {
    const userid = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      _id: userid,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/view_profile`,
        data,
        options
      );
      setLists([response.data.data]);
      getHomeData2();
    } catch (err) {
      //console.error(err);
    }
  };

  useEffect(() => {
    getHomeData22();
  }, []);

  const getHomeData22 = async () => {
    const userid = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: userid,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/get_my_channel`,
        data,
        options
      );

      if (response.data.data && response.data.data.length > 0) {
        const channelName = response.data.data[0].channel_name;
        const channelid = response.data.data[0]._id;

        secureLocalStorage.setItem("channel_name", channelName);
        secureLocalStorage.setItem("channel_id", channelid);

        setListsa(response.data.data);
      }
    } catch (err) {
      //console.error(err);
    }
  };
  const imageurl = secureLocalStorage.getItem("imageurl");

  useEffect(() => {
    getHomeData3();
  }, []);

  const getHomeData3 = async () => {
    const userid = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: userid,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/notification_count`,
        data,
        options
      );
      setCount(response.data);
      //console.log("count", response.data);
    } catch (err) {
      //console.error(err);
    }
  };

  const userId = secureLocalStorage.getItem("_id");
  const location = useLocation();
  const isProtectedPage = [
    //   "/myaccount",
    //   "/setting",
    //   "/upload_Video",
    //   "/uploadmusic",
    //   "/appp",
    //   "/report",
    //   "/notification",
    //   "/history_page",
    //   "/history_page",
    //   "/referral",
    //   "/setting",
    //   "/myaccount2",
    //   "/working",
    //   "/forgotpassword",
  ].includes(location.pathname);

  const removeToken = async () => {
    // const getHomeData3 = async () => {
    const userid = secureLocalStorage.getItem("_id");
    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      _id: userid,
    };
    //console.log("dataasaaaa",data)
    try {
      const response = await axios.patch(
        `${apiurl[0].apiUrl}api/logout`,
        data,
        options
      );
      setCount(response.data);
      window.location.reload("/login");
      //console.log("aasdasdresponse.data",response.data)
    } catch (err) {}
    secureLocalStorage.removeItem("_id");
    secureLocalStorage.removeItem("username");
    secureLocalStorage.removeItem("email");
    secureLocalStorage.removeItem("password");
    secureLocalStorage.removeItem("confirm_password");
    secureLocalStorage.removeItem("gender");
    secureLocalStorage.removeItem("fcmm");
    secureLocalStorage.removeItem("channel_name");
    secureLocalStorage.removeItem("role");
    secureLocalStorage.removeItem("channel_id");
    secureLocalStorage.removeItem("videoiid");
    secureLocalStorage.removeItem("userId");
    secureLocalStorage.removeItem("useridd");
    secureLocalStorage.removeItem("channelid");
    secureLocalStorage.removeItem("categorytpee");
    secureLocalStorage.removeItem("getsingleaudio");
    secureLocalStorage.removeItem("comentidd");
    secureLocalStorage.removeItem("getsinglevideo");
    secureLocalStorage.removeItem("livechanennelname");
    secureLocalStorage.removeItem("livetoken");
    secureLocalStorage.removeItem("channel_id");
    secureLocalStorage.removeItem("channel_name");
    secureLocalStorage.removeItem("channelidaudio");
    secureLocalStorage.removeItem("vaideoiid");
    secureLocalStorage.removeItem("sizeofivideo");
    localStorage.removeItem("yuonair_fcm");
    localStorage.removeItem("imageurl");
    localStorage.removeItem("facebook_id");
    localStorage.removeItem("google_id");
    localStorage.removeItem("twitter_id");
    localStorage.removeItem("name");
  };

  if (!userId && isProtectedPage) {
    return <Navigate to="/login" />;
  }

  let searchvalue = (e) => {
    axios
      .get(`${apiurl[0].apiUrl}api/search?term=${e.target.value}`)
      .then((res) => {
        //    console.log(res.data.data)
        setshowsearch(res.data.data);
      });
  };

  let videovalue = (video_value) => {
    //console.log(video_value);
    secureLocalStorage.setItem("video_value", video_value);
    Navigate(`/searchdata`);
  };

  let userid = secureLocalStorage.getItem("_id");

  let userclick = () => {
    Navigate("/login");
  };

  const handleProfileImageSelect = (event) => {
    setSelectedProfileImage(event.target.files[0]);
    setpimage(URL.createObjectURL(event.target.files[0]));
  };

  const handleCoverImageSelect = (event) => {
    setSelectedCoverImage(event.target.files[0]);
    setcimage(URL.createObjectURL(event.target.files[0]));
  };

  const handleFormSubmit = async (event) => {
    event.preventDefault();

    if (!selectedProfileImage || !selectedCoverImage) {
      setError("Please select both profile and cover images.");
      return;
    }
    if (!channelname.trim()) {
      setError("Please enter a channel name.");
      return;
    }
    if (!description.trim()) {
      setError("Please enter a description.");
      return;
    }

    const formData = new FormData();
    formData.append("profile_image", selectedProfileImage);
    formData.append("cover_image", selectedCoverImage);
    formData.append("user_id", usered);
    formData.append("channel_name", channelname);
    formData.append("handle", description);

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/create_channel`,
        formData,

        {
          headers: {
            "Content-Type": "multipart/form-data",
            "X-Api-Key": "your-api-key", // Replace with your actual API key
          },
        }
      );

      setSelectedProfileImage(null);
      setSelectedCoverImage(null);
      setChannelname("");
      setDescription("");
      setError("");
      toast.success(response.data.msg);
      setTimeout(() => {
        // Navigate('/')
      }, 3000);
      // setSuccessMessage("Channel create successfully!");
      // window.location.reload("/")
    } catch (error) {
      //console.error("Error creating channel:", error.response.data);
    }
  };

  return (
    <>
      <ToastContainer />
      <nav className="navbar navbar-expand navbar-light bg-white static-top osahan-nav sticky-top">
        &nbsp;&nbsp;
        <button
          className="btn btn-link btn-sm text-secondary order-1 order-sm-0"
          id="sidebarToggle"
        >
          <i className="fas fa-bars" />
        </button>{" "}
        &nbsp;&nbsp;
        <Link to="/" className="navbar-brand mr-1">
          <img className="img-fluid" alt="" src="img/logo.png" />
        </Link>
        {/* Navbar Search */}
        <form className="d-none d-md-inline-block form-inline ml-auto mr-0 mr-md-5 my-2 my-md-0 osahan-navbar-search">
          <div className="input-group">
            <input style={{borderTopLeftRadius:'20px' , borderBottomLeftRadius:'20px'    }}
              type="text"
              className="form-control"
              placeholder="Search for..."
            />
            <div className="input-group-append">
              <button className="btn btn-light" type="button" style={{borderTopRightRadius:'20px' , borderBottomRightRadius:'20px'    }}>
                <i className="fas fa-search" />
              </button>
            </div>
          </div>
        </form>
        {/* Navbar */}
        <ul className="navbar-nav ml-auto ml-md-0 osahan-right-navbar">
          <li className="nav-item dropdown no-arrow mx-1">
            <Link
              to="#"
              className="nav-link dropdown-toggle"
              id="alertsDropdown"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <img
                style={{ height: "24px", width: "24px" }}
                src="img/addvideo.png"
              />
            </Link>
            <div
              className="dropdown-menu dropdown-menu-right"
              aria-labelledby="alertsDropdown"
            >
              <Link className="dropdown-item" to="/appp">
              <img style={{height:'26px' , width:'26px'}}  src='img/live.png'/> &nbsp; Go Live
              </Link>
              <Link className="dropdown-item" to="/videoupload">
              <img style={{height:'26px' , width:'26px'}}  src='img/upload.png'/> &nbsp; Upload a
                video
              </Link>
              
              <Link className="dropdown-item" to="/musicupload">
              <img style={{height:'24px' , width:'24px'}}  src='img/music.png'/> &nbsp; Upload a Music
              </Link>
            </div>
          </li>

          <li className="nav-item dropdown no-arrow mx-1">
            <Link to='/notification'
              className="nav-link dropdown-toggle"
             
              id="alertsDropdown"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <img style={{height:'24px' , width:'24px'}}  src='img/bell.png'/>
              <span className="badge badge-danger">9+</span>
            </Link>
           
          </li>
        
          <li className="nav-item dropdown no-arrow osahan-right-navbar-user">
            <a
              className="nav-link dropdown-toggle user-dropdown-link"
              href="#"
              id="userDropdown"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              {userid == null ? (
                <img alt="Avatar" src="img/profilepic.png" />
              ) : (
                <>
                  {" "}
                  {lists?.map((list) => {
                    return (
                      <>
                        {list.profile_image && list.profile_image.filename ? (
                          <img
                            className="channel-profile-img"
                            alt="Avatar"
                            src={
                              `${apiurl[0].apiUrl}uploads/` +
                              list.profile_image.filename
                            }
                          />
                        ) : (
                          <img
                            className="channel-profile-img"
                            alt="Avatar"
                            src="img/profilepic.png"
                          />
                        )}
                      </>
                    );
                  })}
                </>
              )}
              {/* <img alt="Avatar" src="img/user.png" /> */}
             
            </a>
            <div
              className="dropdown-menu dropdown-menu-right"
              aria-labelledby="userDropdown"
            >
              <Link to='/singlechannel' className="dropdown-item" >
              <img style={{height:'24px' , width:'24px'}}  src='img/myaccount.png'/> &nbsp; My Account
              </Link>
              <Link to="/subscription" className="dropdown-item">
              <img style={{height:'24px' , width:'24px'}}  src='img/subscribe.png'/> &nbsp; Subscriptions
              </Link>
              <Link to="/watchlater" className="dropdown-item">
              <img style={{height:'24px' , width:'24px'}}  src='img/watchlater.png'/> &nbsp; Watch later
              </Link>
              <Link to="/setting" className="dropdown-item">
              <img   src='img/settings.png'/> &nbsp; Settings
              </Link>
              <Link
                data-toggle="modal"
                data-target="#myModal"
                to="#"
                className="dropdown-item"
              >
               <img style={{height:'24px' , width:'24px'}}  src='img/createchannel.png'/> &nbsp;Create a channel
              </Link>

              <Link
                data-toggle="modal"
                data-target="#myModal1"
                to="#"
                className="dropdown-item"
              >
                <img   src='img/profilepic.png'/> &nbsp;Profile Picture
              </Link>

              {/* <div className="dropdown-divider" /> */}
              {userid == null ? (
                <Link
                  to="/login"
                  className="dropdown-item"
                  href="#"
                  data-toggle="modal"
                  data-target="#logoutModal"
                >
                  <i className="fas fa-fw fa-sign-out-alt" /> &nbsp; Login
                </Link>
              ) : (
                <a
                  className="dropdown-item"
                  href="#"
                  data-toggle="modal"
                  data-target="#logoutModal"
                >
                 <img  style={{height:'24px' , width:'24px'}} src='img/signout.png'/> &nbsp; Logout
                </a>
              )}
            </div>
          </li>
        </ul>
      </nav>
      <div
        className="modal fade"
        id="logoutModal"
        tabIndex={-1}
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div
          className="modal-dialog modal-sm modal-dialog-centered"
          role="document"
        >
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Ready to Leave?
              </h5>
              <button
                className="close"
                type="button"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true"></span>
              </button>
            </div>
            <div className="modal-body">
              Select "Logout" below if you are ready to end your current
              session.
            </div>
            <div className="modal-footer">
              <button
                className="btn btn-secondary"
                type="button"
                data-dismiss="modal"
              >
                Cancel
              </button>
              <Link
                to="/login"
                className="btn btn-primary"
                onClick={removeToken}
              >
                Logout
              </Link>
            </div>
          </div>
        </div>
      </div>
      <div className="modal" id="myModal">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Create Your Channel</h5>
              <button type="button" className="close" data-dismiss="modal">
                
              </button>
            </div>

            <div className="card-footer">
              <font style={{ color: "blue" }}>
                <p>{successMessage}</p>
              </font>
              <font style={{ color: "red" }}>{error && <p>{error}</p>}</font>

              <form onSubmit={handleFormSubmit}>
                {listsa?.map((images) => {
                  return (
                    <div className="modal-body">
                      <div className="single-channel-image">
                        <div style={{ height: "155px", width: "100%" }}>
                          <img
                            style={{ height: "130px" }}
                            width={"100%"}
                            src={
                              `${apiurl[0].apiUrl}uploads/` +
                              images?.image[0]?.filename
                            }
                            alt=""
                            className="img-fluid"
                          />
                        </div>

                        <div
                          className="channel-profile mainprofileimage"
                          style={{ position: "absolute", top: "40px" }}
                        >
                          <img
                            className="channel-profile-img profileimg mainprofile"
                            width={"100%"}
                            src={
                              `${apiurl[0].apiUrl}uploads/` +
                              images?.image[1]?.filename
                            }
                          />
                        </div>
                      </div>
                    </div>
                  );
                })}

                <br />
                <div className="form-group">
                  <label for="profile" className="btn btn-info">
                    Profle-Pic
                  </label>
                  <input
                    onChange={handleProfileImageSelect}
                    style={{ opacity: "0" }}
                    type="file"
                    id="profile"
                  />{" "}
                  <br />
                  <label for="cover" className="btn btn-primary">
                    Cover-Pic
                  </label>{" "}
                  <br />
                  <input
                    onChange={handleCoverImageSelect}
                    style={{ opacity: "0" }}
                    type="file"
                    id="cover"
                  />
                  <br />
                  <label htmlFor="channelname">Channel Name:</label>
                  <input
                    required
                    type="text"
                    placeholder={secureLocalStorage.getItem("channel_name")}
                    value={channelname}
                    onChange={(e) => setChannelname(e.target.value)}
                    className="form-control profile1"
                    id="channelname"
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="description">Description:</label>
                  <input
                    required
                    type="text"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    className="form-control profile2"
                    id="description"
                  />
                </div>
                <br />
                <button className="btn btn-success" type="submit">
                  Submit
                </button>
              </form>
              <div></div>
            </div>
          </div>
        </div>
      </div>

      <div className="modal" id="myModal1">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Your Profile</h5>
              <button type="button" className="close" data-dismiss="modal">
                
              </button>
            </div>

            {lists?.map((list) => {
              return (
                <div className="modal-body">
                  <div className="single-channel-image">
                    {list.profile_image && list.profile_image.filename ? (
                      <img
                        style={{ height: "205px" }}
                        className="img-fluid"
                        alt="Avatar"
                        src={
                          `${apiurl[0].apiUrl}uploads/` +
                          list.profile_image.filename
                        }
                      />
                    ) : (
                      <img
                        style={{ height: "100px" }}
                        className="img-fluid"
                        alt="Avatar"
                        src="img/logo.png"
                      />
                    )}

                    <div className="channel-profile">
                      {lists.map((list) => {
                        return (
                          <>
                            {list.profile_image &&
                            list.profile_image.filename ? (
                              <img
                                className="channel-profile-img"
                                alt="Avatar"
                                src={
                                  `${apiurl[0].apiUrl}uploads/` +
                                  list.profile_image.filename
                                }
                              />
                            ) : (
                              <img
                                className="channel-profile-img"
                                alt="Avatar"
                                src="img/logo.png"
                              />
                            )}
                          </>
                        );
                      })}
                    </div>
                  </div>
                </div>
              );
            })}
            <div>
              {" "}
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <label htmlFor="profile-picture-input">
                <span className="btn btn-info">Profile picture</span>
                <input
                  id="profile-picture-input"
                  type="file"
                  onChange={handleImageSelect}
                  style={{ display: "none" }}
                />
              </label>
              <br />
              <br />
              <form onSubmit={handleFormSubmitt}>
                &nbsp;&nbsp;&nbsp;&nbsp;{" "}
                <button
                  onClick={getHomeData2}
                  className="btn btn-info"
                  type="submit"
                >
                  Updated
                </button>
                <br />
                <br />
                <br />
                <br />
                <br />
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Navbar;
