import {
  FacebookShareButton,
  TwitterShareButton,
  LinkedinShareButton,
  PinterestShareButton,
  WhatsappShareButton,
} from "react-share";
import { BiDotsVerticalRounded } from "react-icons/bi";
import axios from "axios";
import React, { useEffect, useState } from "react";
import apiurl from "./config";
import { Link } from "react-router-dom";
import secureLocalStorage from "react-secure-storage";
import { TfiSave } from "react-icons/tfi";
import { AiOutlineShareAlt } from "react-icons/ai";
import { BiBlock } from "react-icons/bi";
import { ImNotification } from "react-icons/im";
import { RxCross1 } from "react-icons/rx";
import { MdBugReport } from "react-icons/md";

const Featuredvideo = () => {
  const [lists, setLists] = useState([]);
  const [successMessage, setSuccessMessage] = useState("");
  const [successMessageee, setSuccessMessageee] = useState("");
  const [selectedOption, setSelectedOption] = useState("");

  const reportVideo = async () => {
    const useriddd = secureLocalStorage.getItem("_id");
    const idddd = secureLocalStorage.getItem("videoiid");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: useriddd,
      video_id: idddd,
      report: selectedOption,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/report_single_video`,
        data,
        options
      );
      //console.log("sadasddddd",response)
      setSuccessMessageee("Submit successfully!");
    } catch (err) {
      //console.error(err);
    }
  };

  const handleOptionChange = (event) => {
    setSelectedOption(event.target.value);
  };



  const savewatchlater = async () => {
    const idddd = secureLocalStorage.getItem("videoiid");
    const useriddd = secureLocalStorage.getItem("_id");
    const channeliddd = secureLocalStorage.getItem("channelid");
    //console.log("response gettingfff", idddd, useriddd, channeliddd);

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: useriddd,
      channel_id: channeliddd,
      video_id: idddd,
      time_zone: Intl.DateTimeFormat().resolvedOptions().timeZone,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/save_watch_later`,
        data,
        options
      );

      setSuccessMessage("Saved successfully!");
    } catch (err) {
      //console.error(err);
    }
  };

  const blockuser = async () => {
    const useriddd = secureLocalStorage.getItem("_id");
    const channeliddd = secureLocalStorage.getItem("channelid");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: useriddd,
      channel_id: channeliddd,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/block_user`,
        data,
        options
      );
      setSuccessMessage("User blocked successfully!");
    } catch (err) {
      //console.error(err);
    }
  };

  const dontrecommend = async () => {
    const idddd = secureLocalStorage.getItem("videoiid");
    const useriddd = secureLocalStorage.getItem("_id");
    const channeliddd = secureLocalStorage.getItem("channelid");
    //console.log("response gettingfff", idddd, useriddd, channeliddd);

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: useriddd,
      channel_id: channeliddd,
      video_id: idddd,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/dont_recommend_channel`,
        data,
        options
      );

      setSuccessMessage("Dont Recommend!");
    } catch (err) {
      //console.error(err);
    }
  };

  const notinterested = async () => {
    const idddd = secureLocalStorage.getItem("videoiid");
    const useriddd = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: useriddd,

      video_id: idddd,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/not_interested_video`,
        data,
        options
      );

      setSuccessMessage("Not interrested!");
    } catch (err) {
      //console.error(err);
    }
  };


  useEffect(() => {
    getHomeData();
  }, [0]);
  const getHomeData = async () => {
    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const data = JSON.stringify({});

    await axios
      .post(`${apiurl[0].apiUrl}api/get_latest_video`, data, options)
      .then((res) => {
        setLists(res.data.data);
      })
      .catch((err) => {});
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();

    const timeDiff = Math.abs(now - date);

    const years = Math.floor(timeDiff / (1000 * 60 * 60 * 24 * 365));
    if (years > 0) {
      return years === 1 ? "1 year ago" : `${years} years ago`;
    }

    const months = Math.floor(timeDiff / (1000 * 60 * 60 * 24 * 30));
    if (months > 0) {
      return months === 1 ? "1 month ago" : `${months} months ago`;
    }

    const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
    if (days > 0) {
      return days === 1 ? "1 day ago" : `${days} days ago`;
    }

    const hours = Math.floor(timeDiff / (1000 * 60 * 60));
    if (hours > 0) {
      return hours === 1 ? "1 hour ago" : `${hours} hours ago`;
    }

    const minutes = Math.floor(timeDiff / (1000 * 60));
    if (minutes > 0) {
      return minutes === 1 ? "1 minute ago" : `${minutes} minutes ago`;
    }

    return "Just now";
  };
  return (
    <div className="container">
      <div className="video-block section-padding">
        <div className="row">
          <div className="col-md-12">
            <div className="main-title">
              <h4>Latest Videos</h4>
            </div>
          </div>

          {lists?.map((list) => {
            return (
              <div className="col-xl-4 col-sm-6 mb-3">
                <div className="video-card">
                  <div className="video-card-image">
                    <Link to='/videopage'  className="play-icon" >
                      <i className="fas fa-play-circle" />
                    </Link>

                    
                    <Link to='/videopage' >
                      <img
                        className="img-fluid"
                        src={
                          `${apiurl[0].apiUrl}uploads/` +
                          list?.video[1]?.filename
                        }
                        alt=""
                      />
                    </Link>

                    <div className="time">3:50</div>
                    
                  </div>
                  <BiDotsVerticalRounded
                                    onClick={() => {
                                      secureLocalStorage.setItem(
                                        "videoiid",
                                        list._id
                                      );
                                      secureLocalStorage.setItem(
                                        "channelid",
                                        list.channel_id
                                      );
                                    }}
                                    className="doticon"
                                    data-toggle="modal"
                                    data-target="#myModal21"
                                    style={{
                                      paddingTop:'15px',
                                      fontSize: "30px",
                                      float: "right",
                                      cursor: "pointer",
                                      color: "balck",
                                    }}
                                  />

<div className="modal" id="my1Modal">
                                    <div className="modal-dialog">
                                      <div className="modal-content" style={{ bottom: "-220px" ,borderRadius:'19px'}}>
                                        <div className="modal-header">
                                          <h4 className="modal-title">
                                            Shared
                                          </h4>
                                          <button
                                            type="button"
                                            className="close"
                                            data-dismiss="modal"
                                          >
                                            
                                          </button>
                                        </div>

                                        <div className="card-footer">
                                          <FacebookShareButton
                                            url={
                                              `${apiurl[0].apiUrl}uploads/` +
                                              list?.video[0]?.filename
                                            }
                                          >
                                            <img style={{height:'66px'}} className="img-fluid" src="img/facebook.png" alt="fblogo"/>
                                            {/* <BsFacebook /> */}
                                          </FacebookShareButton>
                                          &nbsp;&nbsp;&nbsp;&nbsp;
                                          <TwitterShareButton
                                            url={
                                              `${apiurl[0].apiUrl}uploads/` +
                                              list?.video[0]?.filename
                                            }
                                          >
                                            <img style={{height:'66px'}} className="img-fluid" src="img/t.jpeg" alt="twiiterklogo"/>

                                            {/* <CgTwitter /> */}
                                          </TwitterShareButton>
                                          &nbsp;&nbsp;&nbsp;&nbsp;
                                          <LinkedinShareButton
                                            url={
                                              `${apiurl[0].apiUrl}uploads/` +
                                              list?.video[0].filename
                                            }
                                          >
                                            <img style={{height:'66px'}} className="img-fluid" src="img/l.jpeg" alt="flinklogo"/>


                                            {/* <FaLinkedin /> */}
                                          </LinkedinShareButton>
                                          &nbsp;&nbsp;&nbsp;&nbsp;
                                          <WhatsappShareButton
                                            url={
                                              `${apiurl[0].apiUrl}uploads/` +
                                              list?.video[0]?.filename
                                            }
                                          >
                                            <img style={{height:'66px'}} className="img-fluid" src="img/w.png" alt="twiiterklogo"/>   


                                            {/* <FaWhatsappSquare /> */}
                                          </WhatsappShareButton>
                                          <br />
                                          <br />
                                          <div></div>
                                        </div>
                                      </div>
                                    </div>
                                  </div>





                  <div className="video-card-body" style={{ width: "100% " }}>
                    <div className="single-video-author img"
                      style={{ float: "left", height: "100px" }}
                    >
                      <Link  onClick={() => {secureLocalStorage.setItem(
                                              "channelid",
                                              list.channel_id
                                            );
                                            secureLocalStorage.setItem(
                                              "useridd",
                                              list.user_id
                                            );
                                          }}
                                          to="/viewuserprofile"   className="user-dropdown-link img"  style={{ float: "left", height: "50px", width: '20% ' }}>
                        <img class="single-video-author img" src={
                                              `${apiurl[0].apiUrl}uploads/` +
                                              list?.data[0]?.image[0]?.filename
                                            } alt="" />
                      </Link>
                    </div>
                    <div >
                    <div className="video-title">
                      <a href="#">{list?.description.charAt(0).toUpperCase() + list?.description.slice(1)}</a>
                       </div>

                      <div className=" text-success">
                      <font className="video-page">{list?.channel_name.charAt(0).toUpperCase() + list?.channel_name.slice(1)} {" "}</font>
                        <a  
                          title
                          data-placement="top"
                          data-toggle="tooltip"
                          href="#"
                          data-original-title="Verified"
                        >
                          
                          <i  className="fas fa-check-circle text-success" />
                        </a>
                      </div>
                      <div className="video-view">
                        {list?.video_views} views &nbsp;
                        <i className="fas fa-calendar-alt" />{" "}
                        {formatDate(list?.current_date)}
                      </div>
                      
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>

     <div className="modal" id="myModal21">
          <div className="modal-dialog">
            <div className="modal-content" style={{ bottom: "-220px" , borderRadius:'20px' }}>
              <div className="modal-header">
                <h3>Choose this One </h3>
                 <button type="button" className="close" data-dismiss="modal">
                  
                </button>
              </div>

              <div className="card-body">
                <font style={{ color: "blue" , fontSize:'15px'}}>
                  {successMessage && <p>{successMessage}</p>}
                </font>
                <Link to="#">
                  <div className="p-2">
                    <span>
                    <img src="img/watchlater.png" style={{height:'18px', width:'18px'}}/>
                    </span>
                    <span
                      onClick={savewatchlater}
                      className="ml-4 text-dark"
                      style={{ fontSize: "15px" }}
                    >
                      Save to watch later
                    </span>
                  </div>
                </Link>
                <Link to="#">
                  <div className="p-2">
                    <span>
                      <AiOutlineShareAlt
                        data-toggle="modal"
                        data-target="#my1Modal"
                        style={{ color: "black", fontSize: "20px" }}
                      />
                    </span>
                    <span
                      className="ml-4 text-dark"
                      data-toggle="modal"
                      data-target="#my1Modal"  data-dismiss="modal"
                      style={{ fontSize: "15px" }}
                    >
                      Share
                    </span>
                  </div>
                </Link>
                <Link to="#">
                  <div className="p-2">
                    <span>
                      <BiBlock style={{ color: "black", fontSize: "20px" }} />
                    </span>
                    <span
                      onClick={blockuser}
                      className="ml-4 text-dark"
                      style={{ fontSize: "15px" }}
                    >
                      Block Users
                    </span>
                  </div>
                </Link>
                <Link to="#">
                  <div className="p-2">
                    <span>
                      <ImNotification
                        style={{ color: "black", fontSize: "18px" }}
                      />
                    </span>
                    <span
                      onClick={notinterested}
                      className="ml-4 text-dark"
                      style={{ fontSize: "15px" }}
                    >
                      Not interested
                    </span>
                  </div>
                </Link>
                <Link to="#">
                  <div className="p-2">
                    <span>
                      <RxCross1 style={{ color: "black", fontSize: "18px" }} />
                    </span>
                    <span
                      onClick={dontrecommend}
                      className="ml-4 text-dark"
                      style={{ fontSize: "15px" }}
                    >
                      Don't recommed Channel
                    </span>
                  </div>
                </Link>
                <Link to="#">
                  <div className="p-2">
                    <span>

                      <img data-toggle="modal"
                      data-dismiss="modal"
                      data-target="#myModalreport" src="img/report.png" style={{height:'18px' , width:'18px'}} />
                      {/* <MdBugReport
                        style={{ color: "black", fontSize: "22px" }}
                      /> */}
                    </span>
                    <span
                      className="ml-4 text-dark"
                      data-toggle="modal"
                      data-dismiss="modal"
                      data-target="#myModalreport"
                      style={{ fontSize: "15px" }}
                    >
                      Report
                    </span>
                  </div>
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className="modal" id="myModalreport">
          <div className="modal-dialog">
            <div className="modal-content" style={{ bottom: "-242px" ,borderRadius:'19px'}}>
              <div className="modal-header">
                <button type="button" className="close" data-dismiss="modal">
                  
                </button>
              </div>

              <div className="card-body">
                <font style={{ color: "blue" }}>
                  {successMessageee && <p>{successMessageee}</p>}
                </font>
                <div className="p-2 ">
                  <label className="fonttt">
                    <input
                      type="radio"
                      name="reportOption"
                      value="Sexual content"
                      checked={selectedOption === "Sexual content"}
                      onChange={handleOptionChange}
                    />
                    &nbsp;&nbsp;&nbsp;Sexual content
                  </label>
                  <br />
                  <label className="fonttt">
                    <input
                      type="radio"
                      name="reportOption"
                      value="Violent or repulsive"
                      checked={selectedOption === "Violent or repulsive"}
                      onChange={handleOptionChange}
                    />
                    &nbsp;&nbsp;&nbsp; Violent or repulsive
                  </label>
                  <br />
                  <label className="fonttt">
                    <input
                      type="radio"
                      name="reportOption"
                      value="Harmful or dangerous acts content"
                      checked={
                        selectedOption === "Harmful or dangerous acts content"
                      }
                      onChange={handleOptionChange}
                    />
                    &nbsp;&nbsp;&nbsp; Harmful or dangerous acts content
                  </label>
                  <br />
                  <label className="fonttt"> 
                    <input
                      type="radio"
                      name="reportOption"
                      value="Harmful or dangerous acts content"
                      checked={
                        selectedOption === "Harmful or dangerous acts content"
                      }
                      onChange={handleOptionChange}
                    />
                    &nbsp;&nbsp;&nbsp; Harmful or dangerous acts content
                  </label>
                  <br />
                  <label className="fonttt">
                    <input
                      type="radio"
                      name="reportOption"
                      value="Harmful or dangerous acts content"
                      checked={
                        selectedOption === "Harmful or dangerous acts content"
                      }
                      onChange={handleOptionChange}
                    />
                    &nbsp;&nbsp;&nbsp; Harmful or dangerous acts content
                  </label>
                  <br />
                  <button class="btn btn-outline-primary btn-block" onClick={reportVideo}>Submit</button>
                </div>
              </div>
            </div>
          </div>
        </div> 
    </div>
  );
};

export default Featuredvideo;
