import React from 'react'
import Topcategory from './Topcategory'
import Featuredvideo from './Featuredvideo'
import Popularchannel from './Popularchannel'
import { Link } from 'react-router-dom'
import Navbar from './Navbar'
import Sidebar from './Sidebar'
import Topvideos from './Topvideos'


const Menubar = () => {
  return (
    <div>
       <Navbar/>
        <div id="wrapper">
          {/* Sidebar */}
          <Sidebar/>
          <div id="content-wrapper">
            <div className="container-fluid pb-0">
              <div className="top-mobile-search">
                <div className="row">
                  <div className="col-md-12">   
                    <form className="mobile-search">
                      <div className="input-group">
                        <input type="text" placeholder="Search for..." className="form-control" />
                        <div className="input-group-append">
                          <button type="button" className="btn btn-dark"><i className="fas fa-search" /></button>
                        </div>
                      </div>
                    </form>   
                  </div>
                </div>
              </div>
            
            <h3>---------------------Video Slider Ongoing-------------------</h3>
              <hr />
          {/* <Topcategory/> */}
              {/* <hr className="mt-0" /> */}
            <Featuredvideo/>
            <hr />
            <Topvideos/>
            {/* <Popularchannel/> */}
            </div>
        
           
          </div>
          
        </div>
    </div>
  )
}

export default Menubar
