import React from 'react'
import Navbar from './Navbar'
import Sidebar from './Sidebar'
import axios from "axios";
import { useEffect } from "react";
import { useState } from "react";
import secureLocalStorage from 'react-secure-storage';
import apiurl from './config';


const Subscription = () => {
  const [listss, setListss] = useState([]);
  useEffect(() => {
    get_subscribe();
  }, []);

  const get_subscribe = async () => {
    const userid = secureLocalStorage.getItem("_id");
   

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: userid,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/get_subscribe_channel`,
        data,
        options
      );

      if (response.data.data && response.data.data.length > 0) {
        setListss(response.data.data);
      }
    } catch (err) {
     
    }
  };
  return (
    <div>
 <Navbar/>
    <div id="wrapper">
      {/* Sidebar */}
     <Sidebar/>
      <div id="content-wrapper">
        <div className="container-fluid">
          <div className="video-block section-padding">
            <div className="row">
              {/* <div className="col-md-12">
                <div className="main-title">
                  
                  <h6>Subscriptions</h6>
                </div>
              </div> */}





              {listss.length > 0 ? (
                          listss.map((list, index) => (
              <div className="col-xl-3 col-sm-6 mb-3">
                <div className="channels-card">
                  <div className="channels-card-image">
                    <a href="#"><img className="img-fluid"  src={
                                          `${apiurl[0].apiUrl}uploads/` +
                                          list?.video_data[0]?.video[1]
                                            ?.filename
                                        } alt="" /></a>
                    <div className="channels-card-image-btn"><button type="button" className="btn btn-success btn-sm border-none">Subscribe <strong>{list?.data[0]?.subscribe_count}</strong></button> <button type="button" className="btn btn-warning btn-sm border-none"><i className="fas fa-times-circle" /></button></div>
                  </div>
                  <div className="channels-card-body">
                    <div className="channels-title">
                      <a href="#">
                      {list?.data[0]?.channel_name.charAt(0).toUpperCase() + list?.data[0]?.channel_name.slice(1)}
                        
                         <span title data-placement="top" data-toggle="tooltip" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></span></a>
                    </div>
                    {/* <div className="channels-view">
                      {list?.data[0]?.subscribe_count} subscribers
                    </div> */}
                  </div>
                </div>
              </div>
              ))
              ) : (
                <div className="col-md-12">No channel Subscribe by you.</div>
              )}
            </div>














            </div>
           
          </div>
        </div>
       </div>
     </div>
    
 
    

  
  )
}

export default Subscription
