import { useState } from 'react';
import axios from 'axios';
import React from 'react';
import Sidebar from './Sidebar';

import Footer from './Footer';
import apiurl from "./config";

import secureLocalStorage from 'react-secure-storage';
import Navbar from './Navbar';

const Help = () => {

  const [output, setOutput] = useState('');
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const storedEmail = secureLocalStorage.getItem('email');

  const handleSubmit = (evt) => {
    evt.preventDefault();
    const _id = secureLocalStorage.getItem('_id');

    if (email !== storedEmail) {
      setOutput(
        'Invalid Email-id. Please enter your current Email-id.'
      );
      return;
    }

    axios
      .post(`${apiurl[0].apiUrl}api/help`, {
        user_id: _id,
       
        first_name: firstName,
        last_name: lastName,
        email: email,
        message: message,
      })
      .then((response) => {
        if (response.data.result === 'true') {
          setOutput('Request Submitted successfully.');
        } else {
          setOutput(response.data.message);
        }


        setFirstName('');
        setLastName('');
        setEmail('');
        setMessage('');
      })
      .catch((err) => {
        console.log(err);
      });
  };


  return (
    <div>
      <Navbar />
      <div id="wrapper">
        {/* Sidebar */}
        <Sidebar />
        <div id="content-wrapper">
          <div className="container-fluid upload-details">
            <div className="row">
              <div className="col-lg-12">
                <div className="main-title">
                  <h6>Submit a Request</h6>
                </div>
              </div>
              <div className="col-lg-2">
                <div className="imgplace" />
              </div>
           
            </div>
            <hr />
            <div className="row">
              <div className="col-lg-12">
             

              <font style={{"color":"red"}}>{output && <div>{output}</div>}</font>
                      <form onSubmit={handleSubmit}>
  <div className="form-group">
    <input
      type="text"
      className="form-control"
      placeholder="Enter your first name"
      value={firstName}
      onChange={(e) => setFirstName(e.target.value)}
    />
  </div>
  <div className="form-group">
    <input
      type="text"
      className="form-control"
      placeholder="Enter your last name"
      value={lastName}
      onChange={(e) => setLastName(e.target.value)}
    />
  </div>
  <div className="form-group">
    <input
      type="email"
      className="form-control"
      placeholder="Enter your email"
      value={email}
      onChange={(e) => setEmail(e.target.value)}
    />
  </div>
  <div className="form-group">
    <textarea
      className="form-control"
      placeholder="Enter your message"
      value={message}
      onChange={(e) => setMessage(e.target.value)}
    ></textarea>
  </div>
  <button type="submit" className="btn btn-primary">
    Submit
  </button>
</form>
                
                <hr />
                <div className="terms text-center">
                  <p className="mb-0">
                    There are many variations of passages of Lorem Ipsum
                    available, but the majority <a href="#">Terms of Service</a>{" "}
                    and <a href="#">Community Guidelines</a>.
                  </p>
                  <p className="hidden-xs mb-0">
                    Ipsum is therefore always free from repetition, injected
                    humour, or non
                  </p>
                </div>
              </div>
            </div>
          </div>
          
        </div>
        
      </div>

      <Footer />
    </div>
  )
}

export default Help;
