import React, { useState, useEffect } from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import axios from "axios";
import { useLocation } from "react-router-dom";
import secureLocalStorage from "react-secure-storage";
import apiurl from "./config";
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import './single.css';
const Singlechannel = () => {

  const [getmyupload, setGetmyupload] = useState([]);
  const [getvideoplaylist, setgetvideoplaylist] = useState([]);
  const [liked, setLiked] = useState([]);
  const [getmyuploadmusic, setGetmyuploadmusic] = useState([]);
  const [lists, setLists] = useState([]);
  
  const location = useLocation();


 const isActive = (path) => {
    return location.pathname === path;
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();

    const timeDiff = Math.abs(now - date);

    const years = Math.floor(timeDiff / (1000 * 60 * 60 * 24 * 365));
    if (years > 0) {
      return years === 1 ? "1 year ago" : `${years} years ago`;
    }

    const months = Math.floor(timeDiff / (1000 * 60 * 60 * 24 * 30));
    if (months > 0) {
      return months === 1 ? "1 month ago" : `${months} months ago`;
    }

    const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
    if (days > 0) {
      return days === 1 ? "1 day ago" : `${days} days ago`;
    }

    const hours = Math.floor(timeDiff / (1000 * 60 * 60));
    if (hours > 0) {
      return hours === 1 ? "1 hour ago" : `${hours} hours ago`;
    }

    const minutes = Math.floor(timeDiff / (1000 * 60));
    if (minutes > 0) {
      return minutes === 1 ? "1 minute ago" : `${minutes} minutes ago`;
    }

    return "Just now";
  };











 useEffect(() => {
    getmyuploadmusics();
  }, []);

  const getmyuploadmusics = async () => {
    const userid = secureLocalStorage.getItem("_id");
    

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: userid,
      
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/get_audio`,
        data,
        options
      );

      if (response.data.data && response.data.data.length > 0) {
        setGetmyuploadmusic(response.data.data);
      }
    } catch (err) {
      //console.error(err);
    }
  };


  useEffect(() => {
    getlikevideo();
  }, []);

  const getlikevideo = async () => {
    const idddd = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = JSON.stringify({
      user_id: idddd,
    });

    await axios
      .post(`${apiurl[0].apiUrl}api/get_like_video_list`, data, options)
      .then((res) => {
        setLiked(res.data.data);
      })
      .catch((err) => {
        //console.error("API Error:", err);
      });
  };




  useEffect(() => {
    getmyplaylist();
  }, []);

  const getmyplaylist = async () => {
    const idddd = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = JSON.stringify({
      user_id: idddd,
    });

    await axios
      .post(`${apiurl[0].apiUrl}api/get_my_playlist`, data, options)
      .then((res) => {
        setgetvideoplaylist(res.data.data);
      })
      .catch((err) => {
        //console.error("API Error:", err);
      });
  };


  useEffect(() => {
    getmyvideos();
  }, []);

  const getmyvideos = async () => {
    const userid = secureLocalStorage.getItem("_id");
    const chanelid = secureLocalStorage.getItem("channel_id");

    //console.log("usertdddd", userid);

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: userid,
      channel_id: chanelid,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/get_my_channel_video`,
        data,
        options
      );

      if (response.data.data && response.data.data.length > 0) {
        setGetmyupload(response.data.data);
      }
    } catch (err) {
      //console.error(err);
    }
  };








  useEffect(() => {
    getHomeData2();
  }, []);

  const getHomeData2 = async () => {
    const userid = secureLocalStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      user_id: userid,
    };

    try {
      const response = await axios.post(
        `${apiurl[0].apiUrl}api/get_my_channel`,
        data,
        options
      );

      if (response.data.data && response.data.data.length > 0) {
        const channelName = response.data.data[0].channel_name;
        const channelid = response.data.data[0]._id;
        
        secureLocalStorage.setItem("channel_name", channelName);
        secureLocalStorage.setItem("channel_id", channelid);
        

        setLists(response.data.data);
      }
    } catch (err) {
      
    }
  };







 

  const [activeTab, setActiveTab] = useState(0);

  const handleTabChange = (tabIndex) => {
    setActiveTab(tabIndex);
  };

  return (
    <div>
      <Navbar />
      <div id="wrapper">
        <Sidebar />
        <div className="single-channel-page" id="content-wrapper">
          {lists?.map((list) => {
            return (
              <div className="single-channel-image">
                <img
                  style={{ height: "calc(28.1290322581vw - 1px)" }}
                  className="img-fluid"
                  alt=""
                  src={`${apiurl[0].apiUrl}uploads/` + list?.image[0]?.filename}
                />
                <div className="channel-profile">
                  <img
                    className="channel-profile-img"
                    alt=""
                    src={
                      `${apiurl[0].apiUrl}uploads/` + list?.image[1]?.filename
                    }
                  />
                </div>
              </div>
            );
          })}
          <div className="single-channel-nav">
            <nav className="navbar navbar-expand-lg navbar-light">
              <a className="channel-brand" href="#">
                


                {lists[0]?.channel_name.charAt(0).toUpperCase() + lists[0]?.channel_name.slice(1)}{" "}
                <span
                  title
                  data-placement="top"
                  data-toggle="tooltip"
                  data-original-title="Verified"
                >
                  <i className="fas fa-check-circle text-success" />
                </span>
              </a>
              <button
                className="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon" />
              </button>
              <div
                style={{ fontSize: "16px",fontWeight: 'bold' ,  }}
                className="collapse navbar-collapse"
                id="navbarSupportedContent"
              >
                <ul className="navbar-nav mr-auto">
                  <TabList style={{borderBottom:'0px' ,  marginBottom:'0px' }}>
                    <Tab selectedClassName="my-selected-tab" style={{padding:'0px' }}>
                      <a 
                        className={`nav-link ${activeTab === 0 ? "active" : ""}`}
                        onClick={() => handleTabChange(0)}
                      >
                        VIDEOS
                      </a>
                    </Tab>
                    <Tab style={{padding:'0px'}} selectedClassName="my-selected-tab">
                      <a
                        className={`nav-link ${activeTab === 1 ? "active" : ""}`}
                        onClick={() => handleTabChange(1)}
                      >
                        PLAYLIST
                      </a>
                    </Tab>
                    <Tab style={{padding:'0px'}} selectedClassName="my-selected-tab">
                      <a
                        className={`nav-link ${activeTab === 2 ? "active" : ""}`}
                        onClick={() => handleTabChange(2)}
                      >
                        MUSIC
                      </a>
                    </Tab>
                    <Tab style={{padding:'0px'}} selectedClassName="my-selected-tab">
                      <a
                        className={`nav-link ${activeTab === 3 ? "active" : ""}`}
                        onClick={() => handleTabChange(3)}
                      >
                        LIKEDVIDEO
                      </a>
                    </Tab>
                  </TabList>
                </ul>
                <form className="form-inline my-2 my-lg-0">
                  &nbsp;&nbsp;&nbsp;{" "}
                  <button
                    className="btn btn-outline-danger btn-sm"
                    type="button"
                  >
                    Subscribe <strong>{lists[0]?.subscribe_count}</strong>
                  </button>
                </form>
              </div>
            </nav>
          </div>

          <div className="container-fluid">
            <Tabs selectedIndex={activeTab}>
              <TabPanel>
              <div className="container-fluid">
              <div className="video-block section-padding">
                <div className="row">
               



                  {getmyupload && getmyupload.length > 0 ? (
                          getmyupload.map((list) => (


                  <div className="col-xl-4 col-sm-6 mb-3">
                    <div className="video-card">
                      <div className="video-card-image">
                        <a className="play-icon" href="#"><i className="fas fa-play-circle" /></a>
                        <a href="#"><img className="img-fluid" src={
                                        `${apiurl[0].apiUrl}uploads/` +
                                        list?.video[1]?.filename
                                      } alt="" /></a>
                        <div className="time">3:50</div>
                      </div>
                      <div className="video-card-body">
                        <div className="video-title">
                          <a href="#">
                          
                          {list?.description.charAt(0).toUpperCase() + list?.description.slice(1)}



                          
                           </a>
                        </div>
                        <div className="video-page text-success">
                         
                        
                        
                        {list?.channel_name.charAt(0).toUpperCase() + list?.channel_name.slice(1)}
                        
                          <a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"> <i className="fas fa-check-circle text-success" /></a>
                        </div>
                        <div className="video-view">
                        {list?.video_views} views &nbsp;<i className="fas fa-calendar-alt" /> {formatDate(list?.current_date)}
                        </div>
                      </div>
                    </div>
                  </div>
                 ))
                 ) : (
                   <div className="col-md-12"><h3>No Data found.</h3></div>
                 )}
                </div>
               
              </div>
            </div>
              </TabPanel>
              <TabPanel>
              <div className="container-fluid">
              <div className="video-block section-padding">
                <div className="row">
                 



{getvideoplaylist && getvideoplaylist.length > 0 ? (
                          getvideoplaylist.map((list) => (
                  <div className="col-xl-4 col-sm-6 mb-3">
                    <div className="video-card">
                      <div className="video-card-image">
                        <a className="play-icon" href="#"><i className="fas fa-play-circle" /></a>
                        <a href="#"><img className="img-fluid"  src={
                                        list?.image && list?.image.filename
                                          ? `${apiurl[0].apiUrl}uploads/` +
                                            list?.image?.filename
                                          : ""
                                      } alt="" /></a>
                        <div className="time">3:50</div>
                      </div>
                      <div className="video-card-body">
                        <div className="video-title">
                          <a href="#">                        {list?.name.charAt(0).toUpperCase() + list?.name.slice(1)}  </a>
                        </div>
                        <div className="video-page text-success">
                          Education  <a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></a>
                        </div>
                        <div className="video-view">
                          1.8M views &nbsp;<i className="fas fa-calendar-alt" /> 11 Months ago
                        </div>
                      </div>
                    </div>
                  </div>
                 


                 ))
                        ) : (
                          <div className="col-md-12"><h3>No Data found.</h3></div>
                        )}





                </div>
               
              </div>
            </div>
              </TabPanel>
              <TabPanel>
              <div className="container-fluid">
              <div className="video-block section-padding">
                <div className="row">
                {getmyuploadmusic && getmyuploadmusic.length > 0 ? (
                          getmyuploadmusic.map((list) => (
                  <div className="col-xl-4 col-sm-6 mb-3">
                    <div className="video-card">
                      <div className="video-card-image">
                        <a className="play-icon" href="#"><i className="fas fa-play-circle" /></a>
                        <a href="#"><img className="img-fluid" src={
                                          `${apiurl[0].apiUrl}uploads/` +
                                          list?.music[1]?.filename
                                        } alt="" /></a>
                        <div className="time">3:50</div>
                      </div>
                      <div className="video-card-body">
                        <div className="video-title">
                          <a href="#">                        {list?.music_title.charAt(0).toUpperCase() + list?.music_title.slice(1)}    </a>
                        </div>
                        <div className="video-page text-success">
                          Education  <a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></a>
                        </div>
                        <div className="video-view">
                        {list?.audio_views} views &nbsp;<i className="fas fa-calendar-alt" /> {formatDate(list?.current_date)}
                        </div>
                      </div>
                    </div>
                  </div>
              ))
              ) : (
                <div className="col-md-12"><h3>No Data found.</h3></div>
              )}


                </div>
               
              </div>
            </div>
              </TabPanel>
              <TabPanel>
              <div className="container-fluid">
              <div className="video-block section-padding">
                <div className="row">
                  {/* <div className="col-md-12">
                    <div className="main-title">
                    
                      <h6>Liked Videos</h6>
                    </div>
                  </div> */}





{liked && liked.length > 0 ? (
                        liked.map((list) => (
                  <div className="col-xl-4 col-sm-6 mb-3">
                    <div className="video-card">
                      <div className="video-card-image">
                        <a className="play-icon" href="#"><i className="fas fa-play-circle" /></a>
                        <a href="#"><img className="img-fluid" src={
                                        `${apiurl[0].apiUrl}uploads/` +
                                        list?.channel_data[0]?.image[1]
                                          ?.filename
                                      } alt="" /></a>
                        <div className="time">3:50</div>
                      </div>
                      <div className="video-card-body">
                        <div className="video-title">
                          <a href="#">
                                      
                                      
                                      {list?.video_data[0]?.video_name.charAt(0).toUpperCase() + list?.video_data[0]?.video_name.slice(1)} 
                                       </a>
                        </div>
                        <div className="video-page text-success">
                      
                                        
                                        
                                        
                                        {list?.channel_data[0]?.channel_name.charAt(0).toUpperCase() + list?.channel_data[0]?.channel_name.slice(1)}
                                        
                                              <a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified">   <i className="fas fa-check-circle text-success" /></a>
                        </div>
                        <div className="video-view">
                        {list?.video_data[0]?.video_views} views &nbsp;<i className="fas fa-calendar-alt" /> {formatDate(list?.current_date)}
                        </div>
                      </div>
                    </div>
                  </div>
                 ))
                 ) : (
                   <div className="col-md-12"><h3>No Data found.</h3></div>
                 )}






                </div>
               
              </div>
            </div>
              </TabPanel>
            </Tabs>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Singlechannel;










